* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  flex-basis: 100%;
  width: 100%;
  min-width: 300px;
  color: white;
  text-align: center;
  line-height: 300%;
}

.block:nth-child(1) {
  background-color: red;
}

.block:nth-child(2) {
  background-color: rgb(204, 0, 0);
}

.block:nth-child(3) {
  background-color: rgb(153, 0, 0);
  order: 3;
}

.block:nth-child(4) {
  background-color: rgb(102, 0, 0);
  order: 4;
}

.block:nth-child(5) {
  background-color: rgb(51, 0, 0);
  order: 5;
}

.block:nth-child(6) {
  background-color: rgb(0, 0, 0);
  order: 6;
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }

  .block:nth-child(3) {
    order: 4;
  }

  .block:nth-child(4) {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.33%;
  }

  .block:nth-child(4) {
    order: 6;
  }

  .block:nth-child(5) {
    order: 5;
  }

  .block:nth-child(6) {
    order: 4;
  }
}
